import {
	Box,
	Button,
	Center,
	Image,
	Modal,
	ModalOverlay,
	ModalContent,
	ModalCloseButton,
	Slider,
	SliderTrack,
	SliderFilledTrack,
	SliderThumb,
	Text,
	useDisclosure
} from "@chakra-ui/react";
import { useEffect, useState } from "react";

import Barcode from "./Barcode";

export default function LightBox(props) {
	const { isOpen, onOpen, onClose } = useDisclosure();
	const [is_flipped, set_is_flipped] = useState(false);
	const [scale_factor, set_scale_factor] = useState(1);

	const close_handler = () => {
		props.reset_display();
		set_is_flipped(false);
		onClose();
	}

	const toggle_flip = () => {
		set_is_flipped(!is_flipped);
	}

	useEffect(() => {
		if (props.card_idx != null) {
			onOpen();
		}
	}, [props.card_idx, onOpen]);

	return (
		<Modal isOpen={isOpen} onClose={close_handler}>
			<ModalOverlay />
			<Button 
				bg="transparent" 
				color="white" 
				opacity=".5" 
				pos="fixed" 
				right="5px" 
				top="5px" 
				fontSize="1.5em"
				onClick={close_handler}
			>
				x
			</Button>
			<ModalContent bg="transparent">
				<Center 
					w="100vw"
					h="140vw"
					sx={{"perspective": "1000px"}}
				>
					<Center 
						w="100%"
						h="100%"
						transform={is_flipped ? "rotateY(-180deg)" : "none"}
						transition="transform .8s"
						sx={{"transformStyle": "preserve-3d"}}
						onClick={toggle_flip}
					>
						<Image 
							src={`${process.env.PUBLIC_URL}/assets/images/cards/${props.card_idx < 10 ? `0${props.card_idx}` : props.card_idx}.png`}
							alt={`Item#${props.card_idx}`} 
							w="100%"
							pos="absolute"
							sx={{"backfaceVisibility": "hidden"}}
						/>
						<Center 
							w="100%" 
							h="100%" 
							bg="#ffffff"
							pos="absolute"
							transform="rotateY(180deg)"
							sx={{"backfaceVisibility": "hidden"}}
						>
							<Box pos="absolute" right="24px">
								<Slider 
									aria-label="barcode-scaling-slider"
									min={0.5} 
									max={1.5} 
									step={0.025}
									defaultValue={scale_factor} 
									minH="100vw"
									orientation="vertical"
									onChange={(value) => set_scale_factor(value)}
								>
									<SliderTrack bg="gray.300">
										<SliderFilledTrack bg={`${props.category_name}.800`} />
									</SliderTrack>
									<SliderThumb 
										boxSize={4} 
										bg={`${props.category_name}.300`}
										sx={{"clipPath": "polygon(0 50%, 50% 0, 100% 50%, 50% 100%)"}} 
									/>
								</Slider>
								{/*<Text  w="100%" textAlign="center" color="gray.500" fontSize=".7em">
									Adjust barcode to be approximately 2in (5cm) long
								</Text>*/}
							</Box>
							<Box transform="rotate(90deg)">
								<Barcode 
									card_idx={props.card_idx} 
									category_idx={props.category_idx} 
									scale={scale_factor} 
								/>
							</Box>
							<Text pos="absolute" bottom="0" w="100%" textAlign="center" color="black">
								RIGHT
							</Text>
						</Center>
					</Center>
				</Center>
			</ModalContent>
		</Modal>
	);
}